import { getDefaultScanner, scanImageData } from 'zbar.wasm';
import {
    MultiFormatReader,
    BarcodeFormat,
    DecodeHintType,
    RGBLuminanceSource,
    BinaryBitmap,
    HybridBinarizer,
} from '@zxing/library';
import * as iq from 'image-q';

class BarcodeDecoder {
    constructor() {
        this.zxingReader = null;
        this.zbarScanner = null;
    }
    destroyScanner() {
        this.zbarScanner && this.zbarScanner.destroy();
    }
    async decode(imageData) {
        let decoded = await this.decodeWithZbar(imageData);
        if (decoded && decoded.length) return this.decodeZbarText(decoded);

        decoded = this.decodeWithZxing(imageData);
        return this.decodeZxingText(decoded);
    }
    decodeZbarText(arr) {
        if (!arr || !arr.length) return [];

        let result = [];
        arr.forEach((obj, index) => {
            result[index] = {};
            result[index].text = obj.decode();
            result[index].format = obj.typeName.slice(5);

            let loc = obj.points;
            let x = 0,
                y = 0;
            loc.forEach((point) => {
                x += point.x;
                y += point.y;
            });
            result[index].location = [x / loc.length, y / loc.length];
            // let loc = obj.points;
            // let x = loc[0].x,
            //     y = loc[0].y;
            // if (obj.typeName === 'ZBAR_QRCODE') {
            //     x = (loc[1].x + loc[3].x) / 2;
            //     y = (loc[1].y + loc[3].y) / 2;
            // } else if (loc.length > 5) {
            //     let minX = Infinity,
            //         minY = Infinity,
            //         maxX = 0,
            //         maxY = 0;
            //     for (let i = 0; i < 5; i++) {
            //         if (loc[i].x < minX) minX = loc[i].x;
            //         if (loc[i].y < minY) minY = loc[i].y;
            //     }
            //     for (let i = loc.length - 5; i < loc.length; i++) {
            //         if (loc[i].x > maxX) maxX = loc[i].x;
            //         if (loc[i].y > maxY) maxY = loc[i].y;
            //     }
            //     x = (maxX + minX) / 2;
            //     y = (maxY + minY) / 2;
            // }
            // result[index].location = [x, y];
        });
        result.forEach((item, index) => {
            item.index = index + 1;
            item.total = result.length;
        });
        return result;
    }
    decodeZxingText(arr) {
        if (!arr || !arr.length) return [];

        let result = [];
        arr.forEach((obj, index) => {
            result[index] = {};
            result[index].text = obj.text;
            result[index].format = BarcodeFormat[obj.format];

            let loc = obj.resultPoints;
            let x = loc[0].x,
                y = loc[0].y;
            if (obj.format === 0 || obj.format === 11) {
                x = (loc[0].x + loc[2].x) / 2;
                y = (loc[0].y + loc[2].y) / 2;
            } else if (obj.format === 5) {
                x = (loc[1].x + loc[3].x) / 2;
                y = (loc[1].y + loc[3].y) / 2;
            } else if (obj.format === 10) {
                x = (loc[1].x + loc[2].x) / 2;
                y = (loc[1].y + loc[2].y) / 2;
            } else if (loc.length < 4) {
                x = (loc[0].x + loc[1].x) / 2;
                y = (loc[0].y + loc[1].y) / 2;
            }
            result[index].location = [x, y];
        });
        result.forEach((item, index) => {
            item.index = index + 1;
            item.total = result.length;
        });
        return result;
    }
    async decodeWithZbar(imageData) {
        if (!this.zbarScanner) this.zbarScanner = await getDefaultScanner();
        let results = await scanImageData(imageData, this.zbarScanner);
        dblog('1st scan with zbar', results);
        return results;
    }
    decodeWithZxing(imageData) {
        if (!this.zxingReader) this.zxingReader = new MultiFormatReader();

        let bitmap = imageData;
        let len = bitmap.width * bitmap.height;
        let luminancesUint8Array = new Uint8ClampedArray(len);
        for (let i = 0; i < len; i++) {
            if (bitmap.data[i * 4 + 3] === 0) luminancesUint8Array[i] = 0xff;
            else
                luminancesUint8Array[i] =
                    (306 * bitmap.data[i * 4] +
                        601 * bitmap.data[i * 4 + 1] +
                        117 * bitmap.data[i * 4 + 2] +
                        0x200) >>
                    10;
        }

        let luminanceSource = new RGBLuminanceSource(luminancesUint8Array, bitmap.width, bitmap.height);
        let binaryBitmap = new BinaryBitmap(new HybridBinarizer(luminanceSource));
        let formats = [
            BarcodeFormat.QR_CODE,
            BarcodeFormat.DATA_MATRIX,
            BarcodeFormat.AZTEC,
            BarcodeFormat.PDF_417,
            BarcodeFormat.CODE_128,
            BarcodeFormat.CODE_39,
            BarcodeFormat.CODE_93,
            BarcodeFormat.EAN_8,
            BarcodeFormat.EAN_13,
            BarcodeFormat.UPC_A,
            BarcodeFormat.UPC_E,
            // BarcodeFormat.ITF,
            // BarcodeFormat.RSS_14,
        ];
        let hints = new Map();

        let results = [];
        for (let i = 0; i <= 4; i++) {
            hints.set(DecodeHintType.POSSIBLE_FORMATS, i < 4 ? [formats.shift()] : formats);
            try {
                results.push(this.zxingReader.decode(binaryBitmap, hints));
            } catch (error) {
                continue;
            }
        }
        dblog('2nd scan with zxing', results);
        return results;
    }
    quantize(imageData) {
        let pointContainer = iq.utils.PointContainer.fromImageData(imageData),
            distanceCalculator = new iq.distance.Euclidean(),
            paletteQuantizer = new iq.palette.WuQuant(distanceCalculator, 2),
            palette = null;
        paletteQuantizer.sample(pointContainer);
        palette = paletteQuantizer.quantizeSync();
        let imageQuantizer = new iq.image.NearestColor(distanceCalculator),
            resultPointContainer = imageQuantizer.quantizeSync(pointContainer, palette),
            uint8array = resultPointContainer.toUint8Array(),
            newImageData = new ImageData(
                new Uint8ClampedArray(uint8array),
                imageData.width,
                imageData.height
            );
        return newImageData;
    }
}

export default BarcodeDecoder;
