import BarcodeDecoder from '@/utils/BarcodeDecoder';

self.dblog = function () {
    self.mode === 'development' && console.log('    ', ...arguments);
};
let barcodeDecoder = new BarcodeDecoder();

self.onmessage = async (event) => {
    let decoded = [],
        { imageData, mode, action, key } = event.data;

    self.mode = self.mode || mode;
    self.wasm = self.key || key;

    switch (action) {
        case 'quantize':
            let newImageData = barcodeDecoder.quantize(imageData);
            postMessage({ newImageData });
            break;
        default:
            try {
                decoded = await barcodeDecoder.decode(imageData);
            } catch (error) {
                dblog(error);
            }
            postMessage({ decoded });
            break;
    }
};
